.profile {
  width: 512px;
  height: 700px;

  position: relative; /* 추후 박스 하단에 추가 버튼을 위치시키기 위한 설정 */
  background: whitesmoke;
  border-radius: 16px;
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.04);

  margin: 0 auto; /* 페이지 중앙에 나타나도록 설정 */

  margin-top: 30px;
  margin-bottom: 32px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

@font-face {
  font-family: "YdestreetB";
  src: url("./fonts/YdestreetB.ttf");
}
.image {
  width: 200px;
  height: 200px;
  border-radius: 70%;
  border-width: 3em;
  border: #7d0000;
  overflow: hidden;
  border-style: solid;
  object-fit: cover;
  text-align: center;
  font-size: large;
  justify-content: center;
  align-items: center;
}

@font-face {
  font-family: "YdestreetB";
  src: url("./fonts/YdestreetB.ttf");
}
.small-title {
  color: #7d0000;
  font-size: 35pt;
  font-family: "YdestreetB";
  text-align: center;
}

.inform {
  margin: 20px;
}

.information {
  border: #7d0000;
  border-style: solid;
  padding: 9px;
  min-width: 380px;
  max-height: 300px;
  border-radius: 10px;
}

.nav-bar4 {
  justify-content: center;
  align-items: center;
  position: absolute;
  display: flex;

  bottom: 0;
  min-width: 512px;
}
.title2 {
  text-align: center;
  position: absolute;
  top: 0;
}
